import React, { useState } from "react";
import styled from "styled-components";
import Img1 from "assets/Image/card1.png";
import Img2 from "assets/Image/card2.png";
import Img3 from "assets/Image/card3.png";

const JustCards = () => {
	const Content = [
		{
			keyword: ["# ", "# ", "# "],
			title: ["  ", <br />, "   ."],
			discription: [
				"  ,  ",
				<br />,
				" , ,   ",
				<br />,
				"     ",
				<br />,
				"    .",
			],
		},
		{
			keyword: ["# ", "# "],
			title: [" ", <br />, "   ."],
			discription: [
				"      ",
				<br />,
				"      ",
				<br />,
				"      ",
				<br />,
				"    .",
			],
		},
		{
			keyword: ["# ", "# "],
			title: [" ", <br />, " ."],
			discription: [
				", ,  3     ",
				<br />,
				"  ,   ",
				<br />,
				"  .     ",
				<br />,
				"   .",
			],
		},
	];

	const CardSetContainer = styled.div`
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	`;
	const CardSet = styled.div`
		display: flex;
		width: 1000px;
		height: 360px;
		border-radius: 40px;
		background-color: ${(props) => props.theme.textColor};
		overflow: hidden;
		margin: 30px 0;
		box-sizing: border-box;
	`;

	const Card = styled.img`
		background-color: aliceblue;
		width: 500px;
		height: 360px;
		z-index: 2;
		box-sizing: border-box;
	`;

	const CardCover = styled.div`
		background-color: ${(props) => props.theme.pointColor};
		width: 500px;
		height: 360px;
		z-index: 1;
		transform: translateX(-500px);
	`;

	const KeyWord = styled.strong`
		display: block;
		color: ${(props) => props.theme.pointColor};
		font-size: 12px;
	`;
	const Title = styled.strong`
		display: block;
		color: ${(props) => props.theme.bgColor};
		font-size: 28px;
		letter-spacing: -2px;
		margin-bottom: 8px;
	`;
	const Discription = styled.div`
		color: ${(props) => props.theme.bgColor};
		font-size: 16px;
		letter-spacing: -1px;
	`;
	const TextWrapper = styled.div`
		padding: 76px 0 0 56px;
		background-color: white;
		width: 500px;
		height: 360px;
	`;
	const Point = styled.span`
		color: ${(props) => props.theme.pointColor};
	`;

	const TitleText = styled.strong`
		color: ${(props) => props.theme.textColor};
		font-size: 40px;
		margin: 160px 0;
	`;

	return (
		<section data-scroll-section>
			<CardSetContainer>
				<TitleText>
					<Point></Point>     ?
				</TitleText>
				<CardSet>
					<Card src={Img1}></Card>
					<TextWrapper>
						<KeyWord>{Content[0].keyword}</KeyWord>
						<Title>{Content[0].title}</Title>
						<Discription>{Content[0].discription}</Discription>
					</TextWrapper>
				</CardSet>
				<CardSet>
					<TextWrapper>
						<KeyWord>{Content[1].keyword}</KeyWord>
						<Title>{Content[1].title}</Title>
						<Discription>{Content[1].discription}</Discription>
					</TextWrapper>
					<Card src={Img2}></Card>
				</CardSet>
				<CardSet>
					<Card src={Img3}></Card>
					<TextWrapper>
						<KeyWord>{Content[2].keyword}</KeyWord>
						<Title>{Content[2].title}</Title>
						<Discription>{Content[2].discription}</Discription>
					</TextWrapper>
				</CardSet>
				{/* <CardCover></CardCover> */}
			</CardSetContainer>
		</section>
	);
};

export default JustCards;
