import React from "react";
import styled from "styled-components";
import Image from "assets/Image/test.png";

const Mumo = () => {
	const SquareImg = styled.img`
		height: 560px;
		width: 520px;
	`;

	const MumoContainer = styled.section`
		margin: 0px 300px;
		display: flex;
		justify-content: space-between;
		align-items: center;
	`;

	const TextContainer = styled.div`
		height: 400px;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
	`;

	const Point = styled.strong`
		color: ${(props) => props.theme.pointColor};
	`;

	const MumoDiscription = styled.p`
		font-size: 24px;
		font-weight: 200;
	`;

	const MumoTitle = styled.div`
		font-size: 40px;
		font-weight: 600;
	`;

	return (
		<MumoContainer data-scroll-section>
			<SquareImg src={Image} alt="just picture"></SquareImg>
			<TextContainer>
				<MumoTitle>
					"<Point> </Point> "
				</MumoTitle>
				<MumoDiscription>
					<Point></Point> 10 
					<br /> <Point></Point>  
					<br />
					       .
				</MumoDiscription>
			</TextContainer>
		</MumoContainer>
	);
};

export default Mumo;
